<template>
  <div>
    <Row :gutter="16" style="padding-bottom: 16px">
      <Col span="6">
        <Input v-model="inputText" @input="emptysDearchOrder">
          <Select
            v-model="searchType"
            slot="prepend"
            :transfer="true"
            style="width: 80px"
            @on-change="searchTypeChange"
          >
            <!-- <Option value="-1" key="-1">客户名称</Option>
            <Option value="0" key="0">未送货</Option>
            <Option value="1" key="1">已付款</Option>
            <Option value="2" key="2">欠款</Option>
            <Option value="3" key="3">已过账</Option>-->
            <Option value="-1" key="-1">客户名称</Option>
            <Option value="0" key="0">未过账</Option>
            <Option value="3" key="3">已过账</Option>
          </Select>
          <Button slot="append" icon="ios-search" @click="findOrder"></Button>
        </Input>
      </Col>
      <Col span="2">
        <span style="color:#ed4014;font-size:22px">退货单</span>
      </Col>
    </Row>

    <Table border :columns="titleList" :data="tableData">
      <template slot-scope="{index}" slot="create_time">{{formatDate(tableData[index])}}</template>
      <template slot-scope="{index}" slot="state">{{stateFormat(tableData[index])}}</template>

      <!-- <template slot-scope="{index}" slot="state">
        <Select
          v-model="tableData[index].state"
          :transfer="true"
          :disabled="tableData[index].state == 3"
          @on-change="stateChange(tableData[index])"
        >
          <Option :value="0" key="0">未送货</Option>
          <Option :value="1" key="1">已付款</Option>
          <Option :value="2" key="2">欠款</Option>
          <Option :value="3" key="3" disabled>已过账</Option>
        </Select>
      </template>-->

      <template slot-scope="{index}" slot="action">
        <Button
          type="primary"
          size="small"
          style="margin-right: 5px"
          @click="editModalClick(tableData[index])"
        >查看</Button>
        <Button
          type="error"
          size="small"
          style="margin-right: 5px"
          @click="deleteModal(tableData[index])"
          :disabled="tableData[index].state == 3 || user.authority.substr(4,5) == 0"
        >删除</Button>
        <Button
          type="error"
          size="small"
          @click="stateChange(tableData[index])"
          :disabled="tableData[index].state == 3 || user.authority.substr(0,1) == 0"
        >过账</Button>
      </template>
    </Table>
    <Button @click="loadMore" style="width: 100%" v-if="isShowMoreBtn">加载更多</Button>
    <Modal
      title="修改销售单"
      :closable="false"
      fullscreen
      footer-hide
      closable
      :z-index="99"
      v-model="editModal"
      class-name="vertical-center-modal"
    >
      <Row :gutter="16" style="padding-bottom: 16px" type="flex" align="middle">
        <Col span="4">
          <Input
            readonly
            v-on:click.native="selectCustomerClick"
            v-model="orderData.shop"
            :disabled="saveBtn == '修改'"
          >
            <span slot="prepend">客户名称</span>
          </Input>
        </Col>
        <Col span="3" style="margin-left:40px">
          <h2>总数量：{{countTotalNum}}</h2>
        </Col>
        <Col span="3">
          <h2>
            总金额：
            <span style="color:#ed4014">{{countTotalPrice}}</span>
          </h2>
        </Col>
        <Col span="3">
          <h2>
            实收金额：
            <Input
              @on-blur="formatRealPrice"
              v-model="orderData.real_price"
              type="number"
              :disabled="saveBtn == '修改'"
              style="color:#ed4014;width: 100px"
            ></Input>
          </h2>
        </Col>
        <Col span="1">
          <Button
            type="primary"
            @click="saveSure"
            :disabled="orderData.state == 3 || user.code != orderData.order_write_code"
          >{{saveBtn}}</Button>
        </Col>
        <Col span="1">
          <Button type="primary" @click="printOrder" :disabled="saveBtn != '修改'">打印</Button>
        </Col>
      </Row>

      <Table border :columns="editTitleList" :data="orderData.orderItemList">
        <template slot-scope="{index }" slot="name">
          <Input
            v-model="orderData.orderItemList[index].name"
            readonly
            v-on:click.native="selectCommodityClick(index)"
            type="text"
            :disabled="saveBtn == '修改'"
          />
        </template>
        <template
          slot-scope="{index }"
          slot="specifications"
        >1 * {{orderData.orderItemList[index].specifications}}</template>

        <template slot-scope="{index}" slot="unit">
          <Select
            :disabled="saveBtn == '修改'"
            v-model="orderData.orderItemList[index].unit"
            :transfer="true"
            @on-change="unitChange(orderData.orderItemList[index])"
          >
            <Option value="个" key="个">个</Option>
            <Option value="件" key="件">件</Option>
          </Select>
        </template>
        <template slot-scope="{index}" slot="num">
          <Input
            v-model="orderData.orderItemList[index].num"
            type="number"
            :disabled="saveBtn == '修改'"
          />
        </template>
        <template slot-scope="{index}" slot="price">
          <Input
            v-model="orderData.orderItemList[index].price"
            type="number"
            @on-blur="formatItemPrice(orderData.orderItemList[index])"
            :disabled="saveBtn == '修改'"
          />
        </template>
        <template
          slot-scope="{index}"
          slot="total_price"
        >{{countItemTotalPrice(orderData.orderItemList[index])}}</template>
        <template slot-scope="{index }" slot="action">
          <Button
            type="error"
            size="small"
            @click="deleteSaleModal(index)"
            :disabled="saveBtn == '修改'"
          >删除</Button>
        </template>
      </Table>
      <Button style="width: 100%;" @click="addItem" size="large" :disabled="saveBtn == '修改'">添加商品</Button>
    </Modal>

    <Modal
      title="请选择客户"
      :closable="false"
      fullscreen
      footer-hide
      closable
      v-model="selectCustomerModal"
      class-name="vertical-center-modal"
    >
      <Row :gutter="16" style="padding-bottom: 16px">
        <Col span="6">
          <Input v-model="inputCustomerText" @input="emptysDearchCustomer">
            <Select v-model="searchCustomerType" slot="prepend" style="width: 80px">
              <Option value="shop">商店名称</Option>
              <Option value="name">客户名称</Option>
              <Option value="phone">电话号码</Option>
            </Select>
            <Button slot="append" icon="ios-search" @click="findCustomer" />
          </Input>
        </Col>
      </Row>
      <Table border :columns="customerTitleList" :data="customerList">
        <template slot-scope="{row}" slot="action">
          <Button type="primary" size="small" @click="selectCustomerModalBtn(row)">选择</Button>
        </template>
      </Table>
      <Button @click="loadCustomerMore" style="width: 100%" v-if="isCustomerShowMoreBtn">加载更多</Button>
    </Modal>

    <Modal
      title="请选择商品"
      :closable="false"
      fullscreen
      footer-hide
      closable
      v-model="selectCommodityModal"
      class-name="vertical-center-modal"
    >
      <Row :gutter="16" style="padding-bottom: 16px">
        <Col span="6">
          <Input v-model="inputCommodityText" @input="emptysDearchCommodity">
            <Select v-model="searchCommodityType" slot="prepend" style="width: 80px">
              <Option value="name">商品名称</Option>
              <Option value="bar_code">条形码</Option>
            </Select>
            <Button slot="append" icon="ios-search" @click="findCommodity"></Button>
          </Input>
        </Col>
      </Row>
      <Table border :columns="commodityTitleList" :data="commodityList">
        <template slot-scope="{row}" slot="action">
          <Button type="primary" size="small" @click="selectCommodityModalBtn(row)">选择</Button>
        </template>
      </Table>
      <Button @click="loadCommodityMore" style="width: 100%" v-if="isCommodityShowMoreBtn">加载更多</Button>
    </Modal>
  </div>
</template>

<script>
import { Http_Post } from "../common/Http";
import {
  findOrder,
  deleteOrder,
  updateOrder,
  findOrderItem,
  stateChange
} from "../params/BackSaleP";
import { formatDate, keepTwoDecimalFull } from "../common/Util";
import { EventBus } from "../common/EventBus";

import { findCustomer } from "../params/CustomerP";
import { findCommodity } from "../params/CommodityP";

export default {
  name: "History",
  data() {
    return {
      user: {},
      saveBtn: "修改",
      inputCustomerText: "", // 搜索框中的内容
      searchCustomerType: "shop", // 搜索类型
      customerTitleList: [
        {
          type: "index",
          width: 50,
          align: "center"
        },
        {
          title: "商店名称",
          key: "shop"
        },
        {
          title: "客户名称",
          key: "name"
        },
        {
          title: "电话号码",
          key: "phone"
        },
        {
          title: "地区",
          key: "region"
        },
        {
          title: "操作",
          slot: "action",
          align: "center"
        }
      ], // customer列表头
      customerList: [], // 列表内容customer
      isCustomerShowMoreBtn: true, // 是否显示Customer加载跟多按钮
      selectCustomerModal: false, // 是否显示选择客户的Modal

      inputCommodityText: "", // 搜索框中的内容
      searchCommodityType: "name", // 搜索类型
      commodityTitleList: [
        {
          type: "index",
          width: 50,
          align: "center"
        },
        {
          title: "商品图片",
          key: "img_url"
        },
        {
          title: "商店名称",
          key: "name"
        },
        {
          title: "商品重量",
          key: "weight"
        },
        {
          title: "商品规格",
          key: "specifications"
        },
        {
          title: "出产地",
          key: "home"
        },
        {
          title: "条形码",
          key: "bar_code"
        },
        {
          title: "保质期",
          key: "expire"
        },
        {
          title: "进货价格",
          key: "price_in"
        },
        {
          title: "正发价格",
          key: "price_out"
        },
        {
          title: "批发价格",
          key: "price_out_down"
        },
        {
          title: "库存",
          key: "stock"
        },
        {
          title: "操作",
          slot: "action",
          align: "center"
        }
      ], // commodity列表头
      commodityList: [], // 列表内容commodity
      isCommodityShowMoreBtn: true, // 是否显示Commodity加载跟多按钮
      selectCommodityModal: false, // 是否显示选择商品的Modal

      offset: 0, // 分页加载页标
      limit: 30, // 分页数据量
      readonly: true, // input只读

      editModal: false, // 是否显示修改订单Modal
      offset: 0, // 分页加载页标
      limit: 30, // 分页数据量
      isShowMoreBtn: true, // 是否显示加载跟多按钮
      searchType: "-1", // 搜索类型
      inputText: "", // 搜索框中的内容
      titleList: [
        {
          type: "index",
          width: 50,
          align: "center"
        },
        {
          title: "客户名称",
          key: "shop"
        },
        {
          title: "数量",
          key: "num"
        },
        {
          title: "金额",
          key: "price"
        },
        {
          title: "状态",
          slot: "state"
        },
        {
          title: "创建时间",
          slot: "create_time"
        },
        {
          title: "操作",
          slot: "action",
          align: "center"
        }
      ], // 列表头
      editTitleList: [
        {
          type: "index",
          width: 50,
          align: "center"
        },
        {
          title: "商品名称",
          slot: "name"
        },
        {
          title: "条形码",
          key: "bar_code"
        },
        {
          title: "规格",
          slot: "specifications"
        },
        {
          title: "单位",
          slot: "unit"
        },
        {
          title: "数量",
          slot: "num"
        },
        {
          title: "单价",
          slot: "price"
        },
        {
          title: "金额",
          slot: "total_price"
        },
        {
          title: "操作",
          slot: "action",
          align: "center"
        }
      ], // 修改Modal列表头

      tableData: [], // 列表数据
      itemIndex: 0, // 商品选择条目index记录
      orderData: {
        code: "",
        customer_code: "",
        order_num_code: "",
        order_delivery_code: "",
        order_write_code: "",
        num: 0,
        price: "",
        price_up: "",
        shop: "",
        state: 0,
        real_price: "0.00",
        create_time: "",
        orderItemList: [] // 条目列表
      } // 提交的订单对象
    };
  },
  mounted() {
    EventBus.$on("catBackOrder", ({ code }) => {
      this.findOrder();
    });
  },

  computed: {
    // 格式化实收金额
    formatRealPrice() {
      let _this = this;
      return function() {
        _this.orderData.real_price = keepTwoDecimalFull(
          _this.orderData.real_price
        );
      };
    },
    // 格式化商品金额
    formatItemPrice(row) {
      let _this = this;
      return function(row) {
        row.price = keepTwoDecimalFull(row.price);
      };
    },
    // 计算每一个条目的总金额
    countItemTotalPrice(row) {
      return function(row) {
        return keepTwoDecimalFull(row.num * row.price);
      };
    },
    // 计算总数量
    countTotalNum() {
      this.orderData.num = 0;
      this.orderData.orderItemList.map((item, index) => {
        this.orderData.num += item.num * 1;
      });
      return this.orderData.num;
    },
    // 计算总金额
    countTotalPrice() {
      var count = 0.0;
      this.orderData.orderItemList.map((item, index) => {
        count += parseFloat(item.price * item.num);
      });
      this.orderData.price = keepTwoDecimalFull(count);
      return this.orderData.price;
    },
    //格式化日期
    formatDate(row) {
      return function(row) {
        return formatDate(new Date(Number(row.create_time)), "yyyy-MM-dd");
      };
    },
    // 订单状态格式化
    stateFormat(row) {
      return function(row) {
        // switch (row.state) {
        //   case 0:
        //     return "未送货";
        //   case 1:
        //     return "已付款";
        //   case 2:
        //     return "欠款";
        //   case 3:
        //     return "已过账";
        // }
        switch (row.state) {
          case 0:
            return "未过账";
          case 3:
            return "已过账";
        }
      };
    }
  },
  methods: {
    // 销售单状态改变
    stateChange(row) {
      let _this = this;
      this.$Modal.confirm({
        title: "警告",
        content: "确定过账此订单？",
        onOk: () => {
          row.state = 3; // 过账
          this.$Spin.show();
          Http_Post(
            stateChange(row),
            function(datas) {
              if (datas.msg === 0) {
                _this.$Spin.hide();
                _this.$Message.success("更改成功");
                _this.offset = 0;
                _this.findOrder();
              } else {
                _this.$Spin.hide();
                _this.$Message.error(datas.error);
              }
            },
            function(error) {
              _this.$Spin.hide();
              _this.$Message.error(error);
            }
          );
        },
        onCancel: () => {}
      });
    },
    // 搜索框为空，查询客户
    emptysDearchCustomer() {
      if (this.inputCustomerText === "") {
        this.offset = 0;
        this.findCustomer();
      }
    },
    // 加载跟多客户
    loadCustomerMore() {
      this.offset++;
      this.findCustomer();
    },
    // 确认选择的客户
    selectCustomerModalBtn(row) {
      this.orderData.customer_code = row.code;
      this.orderData.shop = row.shop;
      this.selectCustomerModal = false;
      this.inputCustomerText = "";
      this.searchCustomerType = "shop";
    },

    // 搜索框为空，查询商品
    emptysDearchCommodity() {
      if (this.inputCommodityText === "") {
        this.offset = 0;
        this.findCommodity();
      }
    },
    // 加载跟多商品
    loadCommodityMore() {
      this.offset++;
      this.findCommodity();
    },
    // 确认选择的商品
    selectCommodityModalBtn(row) {
      let orderItem = this.orderData.orderItemList[this.itemIndex];
      if (orderItem.commodity_code != row.code) {
        for (var i = 0; i < this.orderData.orderItemList.length; i++) {
          if (row.code === this.orderData.orderItemList[i].commodity_code) {
            this.$Message.error("销售单中已有此商品");
            return;
          }
        }
      }
      orderItem.commodity_code = row.code;
      orderItem.bar_code = row.bar_code;
      orderItem.specifications = row.specifications;
      orderItem.price = row.price_in;
      orderItem.unit = "个";
      orderItem.name = row.name;
      this.selectCommodityModal = false;
      this.inputCommodityText = "";
      this.searchCommodityType = "name";
    },
    // 添加新商品空条目
    addItem() {
      if (
        this.orderData.orderItemList.length != 0 &&
        this.orderData.orderItemList[this.orderData.orderItemList.length - 1]
          .commodity_code == ""
      ) {
        this.$message.error("上一个条目未选择商品，请选择后再添加新条目");
        return;
      }
      this.orderData.orderItemList.push({
        code: "",
        order_code: "",
        commodity_code: "",
        num: 0,
        bar_code: "",
        unit: "个",
        price: "0.00",
        specifications: 0,
        name: "",
        createTime: ""
      });
    },
    // 删除商品
    deleteSaleModal(index) {
      let _this = this;
      this.$Modal.confirm({
        title: "警告",
        content: "确定删除此商品？",
        onOk: () => {
          if (_this.orderData.orderItemList.length == 1) {
            _this.$Message.error("只剩一个条目，无法删除");
            return;
          }
          _this.orderData.orderItemList.splice(index, 1);
        },
        onCancel: () => {}
      });
    },
    // 显示选择商品的Modal
    selectCommodityClick(index) {
      if (this.saveBtn == "修改") {
        return;
      }
      this.offset = 0;
      this.itemIndex = index;
      this.findCommodity();
      this.selectCommodityModal = true;
    },
    // 显示选择客户的Modal
    selectCustomerClick() {
      if (this.saveBtn == "修改") {
        return;
      }
      this.offset = 0;
      this.findCustomer();
      this.selectCustomerModal = true;
    },

    // 显示修改订单的Modal
    editModalClick(row) {
      this.saveBtn = "修改";
      this.orderData.code = row.code;
      this.orderData.customer_code = row.customer_code;
      this.orderData.order_num_code = row.order_num_code;
      this.orderData.order_delivery_code = row.order_delivery_code;
      this.orderData.order_write_code = row.order_write_code;
      this.orderData.num = row.num;
      this.orderData.price = row.price;
      this.orderData.price_up = row.price_up;
      this.orderData.shop = row.shop;
      this.orderData.state = row.state;
      this.orderData.real_price = row.real_price;
      this.orderData.create_time = row.create_time;
      this.findOrderItem(row);
      this.editModal = true;
    },
    // 条件搜索变化
    searchTypeChange() {
      this.findOrder();
    },
    // 显示删除客户的Modal
    deleteModal(row) {
      let _this = this;
      this.$Modal.confirm({
        title: "警告",
        content: "确定删除此订单？",
        onOk: () => {
          this.$Spin.show();
          Http_Post(
            deleteOrder(row),
            function(datas) {
              if (datas.msg === 0) {
                _this.$Spin.hide();
                _this.$Message.success("删除成功");
                _this.offset = 0;
                _this.findOrder();
              } else {
                _this.$Spin.hide();
                _this.$Message.error(datas.error);
              }
            },
            function(error) {
              _this.$Spin.hide();
              _this.$Message.error(error);
            }
          );
        },
        onCancel: () => {}
      });
    },
    // 加载跟多
    loadMore() {
      this.offset++;
      this.findOrder();
    },
    // 搜索框为空，查询d订单
    emptysDearchOrder() {
      if (this.inputText === "") {
        this.offset = 0;
        this.findOrder();
      }
    },
    // 查询订单
    findOrder() {
      let _this = this;
      this.$Spin.show();
      Http_Post(
        findOrder(_this.inputText, _this.searchType, _this.offset, _this.limit),
        function(datas) {
          if (datas.msg === 0) {
            if (datas.data.length < _this.limit) {
              _this.isShowMoreBtn = false;
            } else {
              _this.isShowMoreBtn = true;
            }
            if (_this.offset == 0) {
              _this.tableData = datas.data;
            } else {
              for (var i = 0; i < datas.data.length; i++) {
                _this.tableData.push(datas.data[i]);
              }
            }
            _this.$Spin.hide();
          } else {
            _this.$Spin.hide();
            _this.$Message.error(datas.error);
          }
        },
        function(error) {
          _this.$Spin.hide();
          _this.$Message.error(error);
        }
      );
    },
    // 查询订单条目
    findOrderItem(order) {
      let _this = this;
      this.$Spin.show();
      Http_Post(
        findOrderItem(order),
        function(datas) {
          if (datas.msg === 0) {
            _this.orderData.orderItemList = datas.data;
            _this.$Spin.hide();
          } else {
            _this.$Spin.hide();
            _this.$Message.error(datas.error);
          }
        },
        function(error) {
          _this.$Spin.hide();
          _this.$Message.error(error);
        }
      );
    },
    // 查询客户
    findCustomer() {
      let _this = this;
      _this.$Spin.show();
      Http_Post(
        findCustomer(
          _this.inputCustomerText,
          _this.searchCustomerType,
          _this.offset,
          _this.limit
        ),
        function(datas) {
          if (datas.msg === 0) {
            if (datas.data.length < _this.limit) {
              _this.isCustomerShowMoreBtn = false;
            } else {
              _this.isCustomerShowMoreBtn = true;
            }
            if (_this.offset == 0) {
              _this.customerList = datas.data;
            } else {
              for (var i = 0; i < datas.data.length; i++) {
                _this.customerList.push(datas.data[i]);
              }
            }
            _this.$Spin.hide();
          } else {
            _this.$Spin.hide();
            _this.$Message.error(datas.error);
          }
        },
        function(error) {
          _this.$Spin.hide();
          _this.$Message.error(error);
        }
      );
    },
    // 查询商品
    findCommodity() {
      let _this = this;
      this.$Spin.show();
      Http_Post(
        findCommodity(
          _this.inputCommodityText,
          _this.searchCommodityType,
          _this.offset,
          _this.limit
        ),
        function(datas) {
          if (datas.msg === 0) {
            if (datas.data.length < _this.limit) {
              _this.isCommodityShowMoreBtn = false;
            } else {
              _this.isCommodityShowMoreBtn = true;
            }
            if (_this.offset == 0) {
              _this.commodityList = datas.data;
            } else {
              for (var i = 0; i < datas.data.length; i++) {
                _this.commodityList.push(datas.data[i]);
              }
            }
            _this.$Spin.hide();
          } else {
            _this.$Spin.hide();
            _this.$Message.error(datas.error);
          }
        },
        function(error) {
          _this.$Spin.hide();
          _this.$Message.error(error);
        }
      );
    },

    // 单位选择单价改变
    unitChange(row) {
      if (row.unit === "个") {
        if (row.specifications === 0) {
          row.price = keepTwoDecimalFull(0);
        } else {
          row.price = keepTwoDecimalFull(row.price / row.specifications);
        }
      } else {
        row.price = keepTwoDecimalFull(row.price * row.specifications);
      }
    },
    // 显示删除客户的Modal
    saveSure() {
      // 判断按钮
      let _this = this;
      if (this.saveBtn == "修改") {
        this.saveBtn = "保存";
      } else {
        this.$Modal.confirm({
          title: "警告",
          content: "确定保存此订单？",
          onOk: () => {
            _this.saveOrder();
          },
          onCancel: () => {}
        });
      }
    },
    // 保存销售单
    saveOrder() {
      // 空判断
      if (
        this.orderData.customer_code == null ||
        this.orderData.customer_code === ""
      ) {
        this.$Message.error("客户未选择");
        return;
      }
      // 空判断
      if (
        this.orderData.order_write_code == null ||
        this.orderData.order_write_code === ""
      ) {
        this.$Message.error("下单员为空");
        return;
      }
      // 空判断
      if (this.orderData.state == null) {
        this.$Message.error("订单状态为空");
        return;
      }
      // 条目空判断
      if (
        this.orderData.orderItemList == null ||
        this.orderData.orderItemList.length === 0
      ) {
        this.$Message.error("订单没有商品");
        return;
      }
      for (var i = 0; i < this.orderData.orderItemList.length; i++) {
        if (
          this.orderData.orderItemList[i].commodity_code == null ||
          this.orderData.orderItemList[i].commodity_code == ""
        ) {
          this.$Message.error("有条目未选择商品");
          return;
        }
        if (
          this.orderData.orderItemList[i].num == null ||
          this.orderData.orderItemList[i].num == 0
        ) {
          this.$Message.error("有条目未填写商品数量");
          return;
        }
      }
      let _this = this;
      _this.$Spin.show();
      Http_Post(
        updateOrder(this.orderData),
        function(datas) {
          if (datas.msg === 0) {
            _this.$Message.success("保存成功");
            _this.$Spin.hide();
            _this.saveBtn = "修改";
            _this.findOrder();
          } else {
            _this.$Spin.hide();
            _this.$Message.error(datas.error);
          }
        },
        function(error) {
          _this.$Spin.hide();
          _this.$Message.error(error);
        }
      );
    },
    // 打印销售单
    printOrder() {}
  },
  created() {
    // 判断是否登录
    if (sessionStorage.getItem("user") == null) {
      this.$router.push({ name: "login" });
      return;
    }
    this.user = JSON.parse(sessionStorage.getItem("user"));
    this.findOrder();
  }
};
</script>

